.overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Nunito';
  font-size: 1.5rem;
}

.modal {
  /* border-radius: 1rem; */
  /* padding: 1.5rem; */
  /* width: 50%; */
  /* max-width: 450px; */
  overflow: visible;
  background: linear-gradient(to right, #169bf6, #00ebff);
  border: 0.1vw solid #fff;
  border-radius: 1.5rem;
  box-shadow: 0 0 0.5rem 0.1rem #00ebff;
  font-family: 'Nunito';
  /* padding: 1.5rem; */
  margin: 0 auto;
}
